    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    display: grid;
    grid-template-columns: 1fr 5fr;
    min-height: 100vh;
    width: 100vw;
}

header {
    grid-row: 1;
    grid-column: 1/3;
    display: flex;
    align-items: center;
    height: 4rem;
    background-color: #f5f5f5;
    padding: 0.5rem 0 1rem 0.5rem;
}

nav {
    grid-column: 1/2;
    grid-row: 2;
    height: calc(100vh - 4rem);
    background-color: #eee;
    padding: 1em;
}

ul {
    list-style: none;
}

ul li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 0.5rem;
    height: 2.5rem;
}

ul li:hover {
    background-color: rgb(201, 220, 227);
    box-shadow: 0 1px 2px rgba(98, 98, 98, 0.5);
}

ul li.active {
    background-color: lightblue;
    box-shadow: 0 1px 2px rgba(98, 98, 98, 0.5);
}

ul li a {
    flex-grow: 1;
    text-align: left;
    line-height: 2.5rem;
    font-size: 1.2rem;
    text-decoration: none;
    padding-left: 0.5rem;
}

ul a:visited {
    color: inherit;
}

ul li span:hover {
    cursor: pointer;
}

nav button {
    display: block;
    height: 2rem;
    width: 70%;
    border-radius: 1rem;
    border: none;
    box-shadow: 0 1px 2px rgba(98, 98, 98, 0.5), 0 -1px 2px rgba(98, 98, 98, 0.5);
    margin: auto;
    margin-top: 2rem;
    font-size: 1rem;
}



nav button:hover {
    cursor: pointer;
    background-color: #f5f5f5;
}

::backdrop {
    background: rgba(0, 0, 0, 0.4);
}

.project-modal,
.task-modal,
.confirm-modal-content {
    margin: 4rem auto;
    padding: 1.5rem;
    border: none;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    max-width: 400px;
    width: 100%;
}

.project-modal h3,
.task-modal h3 {
    text-align: center;
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.project-modal form p,
.task-modal form p {
    margin-bottom: 1rem;
}

.project-modal input,
.project-modal form label,
.task-modal input,
.task-modal select,
.task-modal form label {
    display: block;
    width: 90%;
    margin: 0 auto;
}

.project-modal form label,
.task-modal form label {
    display: block;
    margin: 0 auto;
    margin-bottom: 0.3em;
}

.project-modal form input,
.task-modal form input,
.task-modal select {
    font-size: 0.9rem;
    padding: 0.2rem 0.2rem;
}

.project-modal p:last-child,
.task-modal p:last-child {
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}

.project-modal button,
.task-modal button,
.confirm-modal-content button {
    width: 40%;
    height: 2rem;
    border: none;
}

.confirm-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
}

.confirm-modal.hidden {
    display: none;
}

.confirm-modal-content {
    background: white;
    text-align: center;
}

.confirm-modal-content p {
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.confirm-modal-content button {
    margin: 1rem 1rem;
    margin-bottom: 1rem;
}

.completed {
    text-decoration: line-through;
    color: gray;
}

button:disabled {
    background-color: #ccc;
    color: #666;
    cursor: not-allowed;
}

main {
    grid-column: 2/3;
    grid-row: 2;
    padding: 1rem;
}

.project-title {
    text-align: center;
    margin-bottom: 2rem;
}

.add-task-button {
    display: block;
    margin: 0 auto;
    margin-bottom: 2rem;
    height: 2rem;
    width: 10rem;
    border-radius: 1rem;
    border: none;
    box-shadow: 0 1px 2px rgba(98, 98, 98, 0.5), 0 -1px 2px rgba(98, 98, 98, 0.5);
    font-size: 1rem;
    letter-spacing: 0.2ch;
}

.task-list-container {
    width: 500px;
    margin: 0 auto;
}

.task {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgb(211, 252, 252);
    margin-bottom: 1rem;
}

.task>* {
    width: 100%;
}

.task>p:last-child {
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.task button {
    background-color: transparent;
    border: none;
}

.task button:hover,
.task button:focus {
    cursor: pointer;
}

.task-details {
    padding: 0.3em 0 0.3em 1em;
    margin-top: 0.4em;
    border-left: 3px solid #ddd;
    font-style: italic;
}

.task.high-priority {
    border-left: 4px solid red;
    background-color: #ffe5e5;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,iBAAiB;IACjB,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,gBAAgB;IAChB,aAAa;IACb,mBAAmB;IACnB,YAAY;IACZ,yBAAyB;IACzB,6BAA6B;AACjC;;AAEA;IACI,gBAAgB;IAChB,WAAW;IACX,0BAA0B;IAC1B,sBAAsB;IACtB,YAAY;AAChB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,iBAAiB;IACjB,cAAc;AAClB;;AAEA;IACI,oCAAoC;IACpC,2CAA2C;AAC/C;;AAEA;IACI,2BAA2B;IAC3B,2CAA2C;AAC/C;;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,mBAAmB;IACnB,iBAAiB;IACjB,qBAAqB;IACrB,oBAAoB;AACxB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,cAAc;IACd,YAAY;IACZ,UAAU;IACV,mBAAmB;IACnB,YAAY;IACZ,6EAA6E;IAC7E,YAAY;IACZ,gBAAgB;IAChB,eAAe;AACnB;;;;AAIA;IACI,eAAe;IACf,yBAAyB;AAC7B;;AAEA;IACI,8BAA8B;AAClC;;AAEA;;;IAGI,iBAAiB;IACjB,eAAe;IACf,YAAY;IACZ,kBAAkB;IAClB,uCAAuC;IACvC,gBAAgB;IAChB,WAAW;AACf;;AAEA;;IAEI,kBAAkB;IAClB,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;;IAEI,mBAAmB;AACvB;;AAEA;;;;;IAKI,cAAc;IACd,UAAU;IACV,cAAc;AAClB;;AAEA;;IAEI,cAAc;IACd,cAAc;IACd,oBAAoB;AACxB;;AAEA;;;IAGI,iBAAiB;IACjB,sBAAsB;AAC1B;;AAEA;;IAEI,UAAU;IACV,cAAc;IACd,aAAa;IACb,8BAA8B;AAClC;;AAEA;;;IAGI,UAAU;IACV,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,eAAe;IACf,MAAM;IACN,OAAO;IACP,WAAW;IACX,YAAY;IACZ,8BAA8B;AAClC;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;IACI,6BAA6B;IAC7B,WAAW;AACf;;AAEA;IACI,sBAAsB;IACtB,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;IAChB,WAAW;IACX,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,cAAc;IACd,mBAAmB;IACnB,YAAY;IACZ,YAAY;IACZ,mBAAmB;IACnB,YAAY;IACZ,6EAA6E;IAC7E,eAAe;IACf,qBAAqB;AACzB;;AAEA;IACI,YAAY;IACZ,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,oCAAoC;IACpC,mBAAmB;AACvB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,aAAa;IACb,WAAW;IACX,8BAA8B;AAClC;;AAEA;IACI,6BAA6B;IAC7B,YAAY;AAChB;;AAEA;;IAEI,eAAe;AACnB;;AAEA;IACI,0BAA0B;IAC1B,iBAAiB;IACjB,2BAA2B;IAC3B,kBAAkB;AACt